  /* Barlow Condensed, Indie Flower, Montserrat, Oswald, Raleway, Open Sans Condensed, Heebo */
 
  
  font-size: 20px !important;
font-weight: 500 !important;
display: inline !important;
margin: auto !important;
margin: 0 !important;
text-align: left !important;
margin-bottom: 8px !important;
}
.username-border {
  display: inline-block !important;
  border-width: 0 0 0 0 !important;
  border-color: var(--own-msg-color) !important;
  border-style: solid !important;

  margin-bottom: 0px !important;
  font-family: Quicksand !important;
  color: var(--title-color) !important;
}
.ce-settings-container > div {
  border-width: 0 !important;
  display: flex;
  flex-direction: column;
}
.image-input {
  display: none;
}
.user-status {
  color: var(--gray-text) !important;
  font-size: 15px !important;
}
.current-user-info {

  display: flex !important;
  flex-direction: column !important;

}
.current-user-info i img {
  cursor: pointer !important;
  transition: 0.2s !important;
}
.current-user-info i img:hover {
  opacity: 0.85 !important;
}
.image-upload-actions {
  display: flex !important;
  justify-content: center;
  margin-top: 10px !important;
  margin-bottom: 10px !important;
   
}
.image-upload-actions button {
  background-color: var(--own-msg-color) !important;
  border-width: 0 !important;
  padding: 8px !important;
  border-radius: 20px !important;
  color: var(--title-color) !important;
  margin: 5px !important;
  cursor: pointer !important;
}
 canvas {
  background-color: rgba(255, 255, 255, 0.1) !important;
  margin-top: 20px !important;

}
.chat-new-avatar {
  margin: auto !important;
}
