}
.ce-section-title {
  border-color: var(--border-color);
border-style: solid;
border-width: 0px 0px 1px 0px;
transition: 0.5s !important;


}
.ce-photo-title:hover, .ce-section-title  {
  background-color: #767f9900 !important
}
.ce-section-title:hover {
  border-color: rgba(255, 255, 255, 0.322) !important;
transition: 0.5s !important;
}
.ce-photo-section {
  padding-left: 12px !important;
padding-right: 12px !important;
border-radius: 15px !important;
padding-top: 5px !important;

}
.gallery-error {
  padding-bottom: 10px !important;
  color: var(--gray-text) !important;
}
.newChatError {
  color: rgb(245, 71, 71) !important;
  padding-left: 20px !important;

}
.anticon-down {
  color: var(--paperclip-color) !important;
  font-size: 24px !important;
  
}
.ce-photo-section:hover {


}
.ce-photo-title-container {
max-height: 50px !important;

}
.ce-photo-title-container:hover {

 
}
.ce-photo-feed {
 
}
.ce-settings-container > div:nth-child(2) {
  padding-left: 12px !important;
  padding-right: 12px !important;
  border-radius: 15px !important;
  margin-bottom: 0 !important;
 
  
}
.ce-settings-container > div:nth-child(2):hover {
  background-color: #767f9900 !important
}
.options-icons {
  display: flex !important;
  justify-content: center !important;

  width: 50px !important;
  cursor: pointer;
margin-top: 20px !important;
margin-bottom: 20px !important;
display: none !important;
}
.chat-bar-options .anticon {
  border-width: 0px !important;
  border-style: solid !important;
  border-color: #c6c4f62f  !important;
  border-radius: 50% !important;
  margin: 5px !important;
  transition: 0.4s;
}
.chat-bar-options  svg {
  padding: 10px !important;
  font-size: 24px !important;
  color: var(--icons-color) !important;
  cursor: pointer;
 
}

.current-user-info img {
  
}
.edit-name-input input {
  background-color: var(--their-msg-color) !important;
  border-width: 0 !important;
  border-radius: 20px !important;
  padding: 10px !important;
  margin: 10px !important;
  color: var(--gray-text) !important;
  max-width: 100% !important;
}
.chat-bar-options  svg:hover  {
  color: #f1f1f1 !important;
  transition: 0.4s;
}
.chat-bar-options .anticon:hover {
  -webkit-transform: scale(1.15);
	transition: 0.4s;
  color: #f1f1f1 !important;
  border-color: #9a97fcce !important;
}
.options-icons-container {
  display: flex !important;
  
  transition: 1s !important;
  flex-direction: column !important;
  justify-content: flex-start !important;
  text-align: left !important;
}
.options-description {
  font-size: 12px !important;
  color: var(--gray-text) !important;
  margin-bottom: 10px !important;
}
.anticon-left path {
  color: var(--paperclip-color) !important;
}
.anticon-logout {
 padding-left: 0px !important;
}
.anticon-delete {
  padding-left: 0px !important;
 }

.anticon-left {
  right: 0 !important;
}
.anticon-left svg {
  line-height: 2px !important;
  font-size: 20px !important;
}
.profile-line {
  width: 70px !important;
  color: var(--paperclip-color) !important;
  padding: 1px !important;
  background-color: var(--paperclip-color) !important;
  margin-top: -10px !important;
}
.chat-container > div:nth-child(1) > div:nth-child(2) > div:nth-child(3) {
 flex: 0 0 0 0 !important;
 min-width: 15px !important;
}

